/*
	1. Header
*/

.header {
	position: relative;
	left: 0;
	top: 15px;
	width: 72px;
	z-index: 100;
	text-align: center;
	opacity: 1;
	visibility: visible;
	@include border-radius(4px);
	margin-right: 8px;
	float: left;

	&.active {
		opacity: 1!important;
		visibility: visible!important;
	}
	.menu-btn {
	    margin-bottom: 8px;
	    position: relative;
	    display: block;
	    height: 72px;
	    text-align: center;
	    background: #ffffff;
	    @include border-radius(4px);
	    z-index: 2;

	    span {
		    margin-left: -13px;
		    margin-top: -1px;
		    position: absolute;
		    left: 50%;
		    top: 50%;
		    display: block;
		    width: 26px;
		    height: 2px;
		    background: $dark-color;
		    @include transition(all 0.3s ease 0s);

		    &:before, &:after {
				content: '';
				position: absolute;
				left: 0;
				top: -7px;
				width: 80%;
				height: 100%;
				background: $dark-color;
				@include transition(all 0.3s ease 0s);
			}
			&:after {
				top: auto;
				bottom: -7px;
				width: 60%;
			}
		}

		&:hover span, &:hover span:before, &:hover span:after {
			background: $active-color;
		}

		&:hover span:before {
			width: 100%;
		}

		&:hover span:after {
			width: 100%;
		}
	}
	.top-menu {
		width: 100%;
		font-size: 0;
		ul {
			margin: 0;
			padding: 0;

			li {
				display: block;
				@include transition(color 0.3s ease 0s);
				&:first-child {
					a {
						@include border-radius(4px 4px 0 0);
					}
				}
				&:last-child {
					a {
						@include border-radius(0 0 4px 4px);
						border: none;
						&:before {
							display: none;
						}
					}
				}
				a {
					position: relative;
					overflow: hidden;
					display: block;
					height: auto;
					color: $dark-color;
					text-align: center;
					background: $white-color;
					padding: 13px 3px;
					font-size: 12px;
					font-weight: 500;
					text-transform: uppercase;

					&:before {
						content: '';
						position: absolute;
						left: 0;
						bottom: 0;
						width: 100%;
						height: 1px;
						background: -moz-radial-gradient(right, ellipse cover, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
						background: -webkit-radial-gradient(right, ellipse cover, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
						background: radial-gradient(ellipse at right, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
					}
					.icon {
						margin: 0 auto;
						position: relative;
						top: 0;
						left: 0;
						display: block;
						width: 40px;
						height: 30px;
						font-size: $default-icon-size;
						color: $dark-color;
						line-height: 30px;
						@include border-radius(30px);
						@include transition(all 0.3s ease 0s);
					}
					.link {
						position: absolute;
						left: 0;
						top: 57%;
						width: 100%;
						display: block;
						font-size: $extra2-small-size;
						font-weight: 500;
						text-transform: uppercase;
						@include transition(all 0.3s ease 0s);
					}
					ul {
						display: none;
					}
				}
				&:hover, &.active {
					a {
						color: $active-color;
						.icon, .link {
							color: $active-color;
						}
					}
				}
			}
		}
	}
	@media (max-width: 1023px) {
		position: fixed;
		left: 0;
		top: -4px;
		width: 100%;
		height: 72px;
		display: flex;
		align-items: center;
		justify-content: center;
		.top-menu {
			margin: 0 auto;
			max-width: 540px;
			display: flex;
			box-shadow: 0 0 25px rgba(0,0,0,0.05);
			ul {
				display: flex;
				width: 100%;
				li {
					display: block;
					width: 100%;
					&:first-child {
						a {
							@include border-radius(4px 0 0 4px);
						}
					}
					&:last-child {
						a {
							@include border-radius(0 4px 4px 0);
						}
					}
					&:last-child a:before {
						display: block;
					}
					a {
						&:before {
							content: '';
							position: absolute;
							left: 0;
							bottom: 0;
							width: 1px;
							height: 100%;
							background: -moz-radial-gradient(top, ellipse cover, rgba(197,202,213,0.7) 0%, rgba(255,255,255,0) 70%);
							background: -webkit-radial-gradient(top, ellipse cover, rgba(197,202,213,0.7) 0%,rgba(255,255,255,0) 70%);
							background: radial-gradient(ellipse at top, rgba(197,202,213,0.7) 0%,rgba(255,255,255,0) 70%);
						}
					}
				}
			}
		}
	}
	@media (max-width: 560px) {
		padding: 0 5px;
		width: 100%;
		.top-menu {
			max-width: 100%;
		}
	}
}

.disable-sidebar .header .menu-btn {
	height: 0;
	visibility: hidden;
	opacity: 0;
}

