/*
	2. Card - Started
*/

.card-started {
	position: relative;
	float: left;
	width: 480px;
	height: 100%;
	z-index: 11;
	@include border-radius(4px);

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		box-shadow: 10px 10px 15px rgba(0,0,0,0.05);
		@include border-radius(0 0 4px 0);
	}
	&:after {
		content: '';
		position: absolute;
		left: -15px;
		top: -15px;
		width: 100%;
		height: 100%;
		background: -moz-linear-gradient(-45deg, $extra-active-color 0%, $extra2-active-color 100%);
		background: -webkit-linear-gradient(-45deg, $extra-active-color 0%, $extra2-active-color 100%);
		background: linear-gradient(135deg, $extra-active-color 0%,$extra2-active-color 100%);
		@include border-radius(4px);
	}

	.profile {
		padding: 50px;
		position: relative;
		left: 0%;
		width: 100%;
		height: 100%;
		background: $white-color;
		text-align: center;
		z-index: 10;
		@include border-radius(4px);
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		&.no-photo {
            padding-top: 50px;

            .image {
                display: none;
            }
            .title {
            	margin-top: 240px;
            }
            .slide {
            	height: 57%;
            }
        }
		.slide {
			position: absolute;
			overflow: hidden;
			left: 0;
			top: 0;
			width: 100%;
			height: 52%;
			background-color: $default-color;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: cover;
			@include border-radius(4px 4px 0 0);

			&:before, &:after {
				content: '';
				position: absolute;
				left: -25%;
				bottom: -63%;
				width: 100%;
				height: 70%;
				background: $white-color;
				@include transform(rotate(12deg));
			}
			&:after {
				left: auto;
				right: -25%;
				@include transform(rotate(-8deg));
			}
		}
		.image {
			margin: 80px 0 20px 0;
			position: relative;
			display: inline-block;
			width: 146px;

			&:before {
				content: '';
				position: absolute;
				top: 10px;
				left: -10px;
				width: 100%;
				height: 100%;
				background: -moz-linear-gradient(-45deg, $extra-active-color 0%, $extra2-active-color 100%);
				background: -webkit-linear-gradient(-45deg, $extra-active-color 0%,$extra2-active-color 100%);
				background: linear-gradient(135deg, $extra-active-color 0%, $extra2-active-color 100%);
				@include border-radius(100%);
			}
			img {
				position: relative;
				display: block;
				width: 100%;
				height: auto;
				border: 3px solid $white-color;
				@include border-radius(100%);
			}
		}
		.title {
			margin: 0 0 10px 0;
			font-size: 34px;
			color: $dark-color;
			line-height: $title-size;
			font-weight: 400;
		}
		.subtitle, .typed-cursor {
			position: relative;
			display: inline-block;
			height: 20px;
			margin: 0;
			font-size: $default-size;
			color: $active-color;
			line-height: 20px;
			font-weight: 400;
		}
		.typed-cursor {
		    position: relative;
		    margin-left: 5px;
		    margin-bottom: 0;
		    display: none;
		    -webkit-animation: typed_cursor 1.5s infinite;
		    animation: typed_cursor 1.5s infinite;
		    -webkit-transition-timing-function: linear;
		    transition-timing-function: linear;
		}
		.social {
			position: relative;
			margin: 16px 0 0 0;

			a {
				margin: 0 6px;
				display: inline-block;
				vertical-align: middle;
				.ion, .fab, .fas {
					font-size: $small-icon-size;
					color: $dark-color;
					@include transition(all 0.3s ease 0s);
				}
				&:hover {
					.ion, .fab, .fas {
						color: $active-color;
					}
				}
			}
		}
		.lnk {
			&.active.discover {
				.arrow {
					@include transform(scaleX(-1));
				}
			}
		}
	}
	@media (max-width: 560px) {
		height: 480px;

		&:after {
			display: none;
		}
		
		.profile {
			padding: 135px 25px 25px 25px;

			&.no-photo {
				padding-top: 250px;

				.title {
					margin-top: 0!important;
				}
			}
			.slide {
				height: 200px;
			}
			.image {
				margin: 0 0 15px 0;
				width: 124px;
			}
			.title {
				font-size: 28px;
				line-height: 28px;
			}
		}
	}
}


/*
	3. Cards
*/

.card-inner {
	background: $white-color;
	transform-origin: center left;
	opacity: 0;
	visibility: hidden;
	z-index: 8;
	padding: 0;
	position: absolute;
	overflow: hidden;
	left: 560px;
	right: 0;
	top: 15px;
	bottom: 15px;
	width: auto;
	height: auto;
	border-radius: 0 4px 4px 0;
	-moz-border-radius: 0 4px 4px 0;
	-webkit-border-radius: 0 4px 4px 0;
	-khtml-border-radius: 0 4px 4px 0;
	transition: visibility 1s linear, opacity 0s linear;
	-moz-transition: visibility 1s linear, opacity 0s linear;
	-webkit-transition: visibility 1s linear, opacity 0s linear;
	-o-transition: visibility 1s linear, opacity 0s linear;

	&.animated {
		opacity: 1;
		visibility: visible;
		z-index: 9;
	}
	&.active {
		opacity: 1;
		visibility: visible;
		z-index: 10;
	}
	&.hidden {
		opacity: 0;
		visibility: hidden;
		z-index: 9;
	}
	&:before, &:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 20px;
		height: 30px;
		background: $white-color;
		z-index: 10;
	}
	&:after {
		top: auto;
		bottom: 0;
	}
	.card-wrap {
		padding: 30px 30px 0 30px;
		position: relative;
		height: 100%;
		overflow: auto;
		overflow-x: hidden;
		-webkit-overflow-scrolling: touch;
	}
	@media (max-width: 1072px) {
		width: 115%;
	}
	@media (max-width: 1023px) {
		position: relative;
		margin: 15px 0;
		left: 0;
		top: 0;
		width: auto;
		height: auto;
		opacity: 1;
		visibility: visible;
		.card-wrap {
			padding: 30px 30px 30px 40px;
			overflow: visible;
			height: auto;
		}
		&:last-child {
			margin-bottom: 50px;
		}
	}
	@media (max-width: 560px) {
		margin: 10px 0;
		.card-wrap {
			padding: 30px;
		}
		&:last-child {
			margin-bottom: 10px;
		}
	}
}

@media (max-width: 1199px) {
	.card-started {
		position: relative;
		float: none;
		width: auto;
		height: auto;
	}
	.card-inner {
		position: relative;
		margin: 15px 0;
		left: 0;
		top: 0;
		width: auto;
		height: auto;
		opacity: 1;
		visibility: visible;
		border-radius: 4px;
	}
	.card-inner .card-wrap {
		overflow: visible;
		height: auto;
	}
	.header .top-menu ul li:last-child a:before {
		display: block;
	}
	.card-started .profile {
		height: 600px;
	}
	.card-started .profile.no-photo .slide {
		height: 55%;
	}
	.card-started .profile.no-photo .title {
		margin-top: 200px;
	}
}

@media (max-width: 560px) {
	.card-inner {
		border-radius: 4px;
		-moz-border-radius: 4px;
		-webkit-border-radius: 4px;
		-khtml-border-radius: 4px;
	}
	.card-inner .card-wrap {
		padding: 30px;
	}
	.card-inner:last-child {
		margin-bottom: 10px;
	}
	.card-started .profile {
		height: calc(100vh - 102px);
		min-height: 560px;
	}
}

/* content */
.content {
	margin: 0 0 40px 0;
	position: relative;

	&:last-child {
		margin-bottom: 30px;
	}
	.title {
		margin: 0 0 0 0;
		padding: 0 0 30px 0;
		position: relative;
		font-size: 20px;
		color: $dark-color;
		line-height: 21px;
		font-weight: 600;
		min-height: 51px;
		z-index: 2;

		&:before {
			content: '';
			position: absolute;
			left: -12px;
			top: 0;
			width: 30px;
			height: 30px;
			background: -moz-linear-gradient(-45deg, rgba($active-color, 0.15) 0%, rgba($active-color, 0.01) 100%);
			background: -webkit-linear-gradient(-45deg, rgba($active-color, 0.15) 0%, rgba($active-color, 0.01) 100%);
			background: linear-gradient(135deg, rgba($active-color, 0.15) 0%, rgba($active-color, 0.01) 100%);
			z-index: -1;
			@include border-radius(30px);
		}
		&:after {
			content: '';
			position: absolute;
			height: 1px;
			left: -30px;
			right: 0;
			bottom: 0;
			width: auto;
			background: -moz-radial-gradient(left, ellipse cover, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
			background: -webkit-radial-gradient(left, ellipse cover, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
			background: radial-gradient(ellipse at left, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
		}
		&.no-title {
			&:before {
				display: none;
			}
		}
		.first-word {
			color: $active-color;
		}
		&::first-letter,
		.first-letter::first-letter {
			color: $active-color;
		}
	}
}
.content .post-comments .title .first-word {
	color: $dark-color;
}
.content .post-comments .title::first-letter,
.content .post-comments .first-letter::first-letter {
	color: $dark-color;
}


