/* 4.2 Services */
.content.services {
}

.service-items {
	font-size: 0;
	&.border-line-v {
		&:after {
			margin: 0 0 0 -1px;
			content: '';
			position: absolute;
			left: 50%;
			top: 0;
			width: 1px;
			height: 100%;
			background: -moz-radial-gradient(top, ellipse cover, rgba(197,202,213,0.7) 0%, rgba(255,255,255,0) 70%);
			background: -webkit-radial-gradient(top, ellipse cover, rgba(197,202,213,0.7) 0%,rgba(255,255,255,0) 70%);
			background: radial-gradient(ellipse at top, rgba(197,202,213,0.7) 0%,rgba(255,255,255,0) 70%);
		}
	}
	.service-item {
		text-align: left;
		.icon {
			margin: 0;
			width: 60px;
			height: 60px;
			text-align: center;
			font-size: $large-icon-size;
			color: $active-color;
			background: -moz-linear-gradient(-45deg, $extra-active-color 0%, $extra2-active-color 100%);
