/*
	Sidebar Widgets
*/
.s_overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: #000;
	z-index: 106;
	opacity: 0.6;
	display: none;
}

.content-sidebar {
	position: fixed;
	top: 0;
	left: -438px;
	width: 338px;
	height: 100%;
	background: #fff;
	z-index: 107;
	@include transition(all 0.6s ease 0s);
}

.content-sidebar .close {
	position: absolute;
	top: 0;
	right: -68px;
	width: 68px;
	height: 68px;
	background: #eeeeee;
	cursor: pointer;
	@include border-radius(0 0 4px 0);
	z-index: 10;
}

.content-sidebar .close:before, .content-sidebar .close:after {
	content: '';
	margin: -2px 0 0 -10px;
	position: absolute;
	left: 50%;
	top: 50%;
	width: 20px;
	height: 2px;
	background: $dark-color;
	@include transform(rotate(45deg));
	@include transition(all 0.3s ease 0s);
}

.content-sidebar .close:after {
	@include transform(rotate(-45deg));
}

.content-sidebar .close:hover:before, .content-sidebar .close:hover:after {
	background: #999;
}

.content-sidebar .sidebar-wrap {
	position: relative;
	height: 100%;
	overflow: auto;
	overflow-x: hidden;
	-webkit-overflow-scrolling: touch;
}

.content-sidebar.active {
	left: 0;
}

.content-sidebar .widget-area {
	position: relative;
}

.content-sidebar .profile {
	padding-top: 150px;
	padding-bottom: 0;
	height: auto;
}

.content-sidebar .profile.no-photo {
	padding-top: 270px;
}

.content-sidebar .profile .slide {
	height: 250px;
}

.content-sidebar .main-menu {
	padding: 25px 0;
	display: none;
}

.content-sidebar .main-menu ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

.content-sidebar .main-menu ul li {
	position: relative;
}

.content-sidebar .main-menu ul li:after,
.content-sidebar .main-menu ul li:first-child::before {
	content: '';
	position: absolute;
	left: -25px;
	bottom: 0;
	width: 100%;
	height: 1px;
	background: -moz-radial-gradient(left, ellipse cover, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
	background: -webkit-radial-gradient(left, ellipse cover, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
	background: radial-gradient(ellipse at left, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
}

.content-sidebar .main-menu ul li:first-child:before {
	bottom: auto;
	top: 0;
}

.content-sidebar .main-menu ul li a {
	position: relative;
	padding: 17px 25px;
	display: block;
	font-weight: 500;
	color: $dark-color;
	@include transition(all 0.3s ease 0s);
}

.content-sidebar .main-menu ul li .children_toggle {
	position: absolute;
	top: 0;
	right: 0;
	width: 65px;
	height: 55px;
	z-index: 2;
	cursor: pointer;
}

.content-sidebar .main-menu ul li .icon {
	margin-right: 10px;
	font-size: 16px;
}

.content-sidebar .main-menu ul li.page_item_has_children > a:before,
.content-sidebar .main-menu ul li.page_item_has_children > a:after {
	margin-top: -1px;
	content: '';
	position: absolute;
	right: 25px;
	top: 50%;
	width: 9px;
	height: 2px;
	background: #666;
	@include transform(rotate(-45deg));
}

.content-sidebar .main-menu ul li.page_item_has_children > a:after {
	right: 30px;
	@include transform(rotate(45deg));
}

.content-sidebar .main-menu ul li.current_page_item > a, 
.content-sidebar .main-menu ul li:hover > a,
.content-sidebar .main-menu ul li.current_page_parent > a,
.content-sidebar .main-menu ul li.current_page_ancestor > a {
	text-decoration: none;
	color: $active-color;
}

.content-sidebar .main-menu ul li.page_item_has_children.current_page_item > a:before, 
.content-sidebar .main-menu ul li.page_item_has_children.current_page_item > a:after, 
.content-sidebar .main-menu ul li.page_item_has_children:hover > a:before, 
.content-sidebar .main-menu ul li.page_item_has_children:hover > a:after,
.content-sidebar .main-menu ul li.page_item_has_children.current_page_parent > a:before, 
.content-sidebar .main-menu ul li.page_item_has_children.current_page_parent > a:after,
.content-sidebar .main-menu ul li.page_item_has_children.current_page_ancestor > a:before, 
.content-sidebar .main-menu ul li.page_item_has_children.current_page_ancestor > a:after {
	background: $active-color;
}

.content-sidebar .main-menu ul li ul {
	padding: 10px 0 10px 25px;
	background: #f6f6f6;
	display: none;
}

.content-sidebar .main-menu ul li ul li a {
	position: relative;
	padding: 6px 25px;
	display: block;
	font-weight: 400;
	color: $dark-color;
	font-size: 14px;
}

.content-sidebar .main-menu ul li ul li:before,
.content-sidebar .main-menu ul li ul li:after {
	display: none;
}

.content-sidebar .widget {
	padding: 25px;
}

.content-sidebar .widget tfoot {
	display: none;
}

.content-sidebar .widget th {
	color: #000;
	padding: 5px;
	text-align: center;
}

.content-sidebar .widget td {
	padding: 5px;
	text-align: center;
}

.content-sidebar .widget label {
	padding-bottom: 0;
}

.content-sidebar .widget ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

.content-sidebar .widget ul ul {
	margin-top: 5px;
	margin-left: 0;
	margin-bottom: 5px;
	padding: 5px 10px;
	font-size: 14px;
}

.content-sidebar .widget ul ul ul {
	margin-top: 5px;
	padding-left: 0;
	margin-left: 0;
	margin-bottom: 0;
	padding-top: 0;
	padding-right: 0;
	padding-bottom: 0;
}

.content-sidebar .widget ul li {
	position: relative;
	padding: 10px 0;
	margin: 0;
	color: #999;
	border-top: 1px solid #eee;
}

.content-sidebar .widget ul li:last-child {
	border-bottom: 1px solid #eee;
}

.content-sidebar .widget ul li a {
	padding-bottom: 0;
	color: $dark-color;
}

.content-sidebar .widget ul li a:hover {
	color: $active-color;
}

.content-sidebar .widget ul li li {
	position: relative;
	overflow: hidden;
	margin: 5px 0 0 0;
	padding: 0;
	padding-left: 15px;
	border: none;
}

.content-sidebar .widget ul li li:before {
	margin: 0;
	content: '';
	position: absolute;
	left: 0;
	top: 8px;
	width: 4px;
	height: 4px;
	background: $active-color;
	@include border-radius(4px);
}

.content-sidebar .widget ul li li li {
	padding-top: 0;
	padding-bottom: 0;
}

.content-sidebar .widget ul li li li li {
	padding-left: 10px;
}

.content-sidebar .widget ul li li li {
	padding-top: 0;
	padding-bottom: 0;
}

.content-sidebar .widget ul li li li:before {
	top: 8px;
}

.content-sidebar .widget ul li li:last-child {
	border-bottom: none;
}

.content-sidebar .widget.widget_recent_comments a, .content-sidebar .widget.widget_recent_entries a {
	padding-top: 0;
	font-weight: 400;
	display: block!important;
}

.content-sidebar .widget ul li .post-date {
	margin: 0;
	display: inline-block;
	padding: 0;
	height: 20px;
	line-height: 18px;
	font-weight: 400;
	font-size: 14px;
	color: #999;
}

.content-sidebar .widget img {
	max-width: 100%;
	height: auto;
}

.content-sidebar .widget .rss-date, .content-sidebar .widget cite {
	margin: 5px 0 10px 0;
	padding: 0;
	display: block;
	height: 20px;
	line-height: 18px;
	font-weight: 400;
	font-size: 14px;
	color: #999;
}

.content-sidebar .widget cite {
	margin: 10px 0 0 0;
}

.content-sidebar .widget .rssSummary {
	color: $dark-color;
}

.content-sidebar span.screen-reader-text {
	display: none;
}

.content-sidebar span.screen-reader-text span {
	@include box-shadow(none);
}

.search-form input.search-field {
	border: solid 1px #ddd;
	width: 100%;
	height: 50px;
	margin: 0;
	padding: 0 50px 0 15px;
	box-shadow: none;
	font-weight: 400;
	-webkit-appearance: textfield;
}

.search-form input.search-submit {
	margin-top: -50px;
	margin-right: 0;
	padding: 0;
	float: right;
	width: 50px;
	height: 50px;
	background: url(../images/android-search.png) no-repeat center center;
	background-size: 18px 18px;
	font-size: 0;
	border: none;
}

.search-form input.search-submit:hover {
	background: url(../images/android-search.png) no-repeat center center;
	background-size: 18px 18px;
}

.content-sidebar #results-container {
	padding-top: 10px;
}

.content-sidebar h2.widget-title {
	margin: 0 0 15px 0;
	padding: 0 0 5px 0;
	position: relative;
	font-size: 18px;
	color: $dark-color;
	line-height: 21px;
	font-weight: 600;
}

.content-sidebar h2.widget-title .first-word {
	color: $active-color;
}

.content-sidebar h2.widget-title::first-letter,
.content-sidebar h2.widget-title .first-letter::first-letter {
	color: $active-color;
}

.content-sidebar h2.widget-title:before {
	content: '';
	position: absolute;
	left: -12px;
	top: 0;
	width: 30px;
	height: 30px;
	background: -moz-linear-gradient(-45deg, rgba($active-color, 0.15) 0%, rgba($active-color, 0.01) 100%);
	background: -webkit-linear-gradient(-45deg, rgba($active-color, 0.15) 0%, rgba($active-color, 0.01) 100%);
	background: linear-gradient(135deg, rgba($active-color, 0.15) 0%, rgba($active-color, 0.01) 100%);
	z-index: -1;
	@include border-radius(30px);
}

.content-sidebar .recentcomments {
	color: #999;
}

.content-sidebar .recentcomments a {
	padding-top: 6px;
	display: block!important;
}

.content-sidebar .recentcomments a.url {
	margin: 0;
	padding: 0;
	display: inline-block;
	height: 20px;
	line-height: 18px;
	font-weight: 400;
	font-size: 14px;
	color: #999;
}

.content-sidebar .recentcomments .comment-author-link {
	margin: 0;
	padding: 0;
	display: inline-block;
	height: 20px;
	line-height: 18px;
	font-weight: 400;
	font-size: 14px;
	color: #999;
}

.content-sidebar .tagcloud a {
	margin: 4px 2px;
	display: inline-block;
	vertical-align: bottom;
	padding: 0 5px;
	font-weight: 400;
	font-size: 13px;
	color: $active-color;
	border: 1px solid $active-color;
}

.content-sidebar .screen-reader-text {
	display: none;
}

.content-sidebar .widget select {
	width: 100%;
	height: 34px;
}

.content-sidebar table {
	margin: 0;
}

.content-sidebar .widget.widget_media_image {
	max-width: 100%;
}

@media (max-width: 1120px) {
	.content-sidebar .sidebar-wrap {
		overflow: auto;
		-webkit-overflow-scrolling: touch;
	}
	.content-sidebar .main-menu {
		padding: 25px;
		display: block;
	}
	.content-sidebar .main-menu ul li:after, .content-sidebar .main-menu ul li:first-child::before {
		left: 0;
	}
}

@media (max-width: 680px) {
	html.sidebar-open, body.sidebar-open {
		height: 100vh;
		overflow: hidden;
	}
	.content-sidebar {
		left: -100%;
		width: 100%;
	}
	.content-sidebar.active {
		left: 0;
	}
	.content-sidebar .sidebar-wrap {
		padding-top: 68px;
	}
	.content-sidebar .close {
		right: 0;
		@include border-radius(0);
		width: 100%;
	}
}

