/* About */
.text-box p {
	margin-top: 0;
}

.content.about p {
	margin-bottom: 0;
}

.info-list {
	padding-top: 0;
}

.content.contacts .info-list ul {
	margin: -11px -30px 0 -30px;
	font-size: 0;
}

.info-list ul li {
	padding: 11px 0;
	position: relative;
	display: block;
	vertical-align: top;
	width: auto;
	font-size: $default-size;
	word-break: break-word;
	text-align: right;
}

.content.contacts .info-list ul li {
	padding: 11px 30px;
	display: inline-block;
	vertical-align: top;
	width: 50%;
}

.info-list ul li:first-child {
	padding-top: 0;
}

.content.contacts .info-list ul li:first-child {
	padding-top: 11px;
}

.info-list ul li:last-child {
	padding-bottom: 0;
}

.content.contacts .info-list ul li:last-child {
	padding-bottom: 11px;
}

.info-list ul li:last-child:before {
	display: none;
}

.content.contacts .info-list ul li:last-child:before {
	display: block;
}

.info-list ul li:before {
	content: '';
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 1px;
	background: -moz-radial-gradient(center, ellipse cover, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
	background: -webkit-radial-gradient(center, ellipse cover, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
	background: radial-gradient(ellipse at center, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
}

.info-list ul li strong {
	float: left;
	margin-right: 8px;
	padding: 1px 8px;
	background: $active-color;
	font-size: 14px;
	line-height: 20px;
	color: #fefefe;
	font-weight: 400;
	text-transform: none;
	border-radius: 2px;
	-moz-border-radius: 2px;
	-webkit-border-radius: 2px;
	-khtml-border-radius: 2px;
}

.info-list ul li strong:after {
	margin-left: 0!important;
}

@media (max-width: 1199px) {
	.content.about .row .col:first-child {
		padding-bottom: 0;
	}
	.info-list ul li, .content.contacts .info-list ul li {
		padding: 11px 30px;
		display: block;
		width: 100%;
	}
	.content.about .info-list ul {
		margin: -11px -30px 0 -30px;
	}
}

@media (max-width: 560px) {
	.info-list ul li {
		display: block;
		width: auto;
	}
	.content.about .info-list ul {
		margin: -11px -30px 0 -30px;
	}
	.content.contacts .info-list ul {
		margin: -11px auto 0 auto;
	}
	.content.contacts .info-list ul li {
		padding: 11px 0;
		display: block;
		vertical-align: top;
		width: auto;
	}
	.content.contacts .info-list ul li:first-child {
		padding-top: 11px;
	}
	.content.contacts .info-list ul li:last-child {
		padding-bottom: 11px;
	}
	.content.contacts .info-list ul li:last-child:before {
		display: block;
	}
}

/* Services */
.service-items.border-line-v:after {
	background: -moz-radial-gradient(top, ellipse cover, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
	background: -webkit-radial-gradient(top, ellipse cover, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
	background: radial-gradient(ellipse at top, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
}

.service-items .service-item {
	text-align: center;
}

.service-items .service-item .icon {
	margin: 0 auto;
	color: #fff!important;
	background: $active-color!important;
}

.service-items .service-item .name {
	margin: 10px 0 5px 0;
	font-size: 17px;
	color: $dark-color;
	font-weight: 500;
	text-transform: none;
}

.service-items .service-item p {
	margin: 0;
}

@media (max-width: 560px) {
	.service-items .col:last-child:after {
		display: none;
	}
}

/* Pricing */
.pricing-items .pricing-item {
	position: relative;
	padding: 0 0 100px 0;
}

.pricing-items .pricing-item .name {
	color: $dark-color;
	font-size: 17px;
	text-transform: none;
}

.pricing-items .pricing-item .amount .number {
	color: $dark-color;
}

.pricing-items .pricing-item .amount .dollar,
.pricing-items .pricing-item .amount .period {
	color: $dark-color;
	font-size: $default-size;
}

.pricing-items .pricing-item .amount .period {
	color: $default-color;
}

.pricing-items .pricing-item .feature-list ul li {
	margin: 10px 0;
	font-size: $default-size;
	color: $default-color;
}

.pricing-items .pricing-item .feature-list ul li:last-child {
	margin-bottom: 0;
	border-bottom: none;
}

.pricing-items .pricing-item .feature-list ul li strong {
	font-size: 13px;
	color: $dark-color;
}

.pricing-items .pricing-item .lnks .lnk .ion {
	top: -2px;
}

/* Fun Fact */
.fuct-items .fuct-item .name {
	margin: 0;
	font-size: $default-size;
	font-weight: 400;
	color: $default-color;
	text-transform: none;
}

/* Clients */
.client-items .client-item img {
	transition: all 0.3s ease 0s;
	-moz-transition: all 0.3s ease 0s;
	-webkit-transition: all 0.3s ease 0s;
	-o-transition: all 0.3s ease 0s;
}

.client-items .client-item img:hover {
	opacity: 0.8;
}

/*
	Resume
*/
.resume-title,
.skill-title {
	padding: 0 0 30px 0;
}

.resume-title:after,
.skill-title:after {
	left: 0;
}

.resume-title .icon,
.skill-title .icon {
	top: -13px;
}

.resume-title .name,
.skill-title .name {
	line-height: 14px;
	color: $dark-color;
	font-size: 17px;
}

.resume-title .name:first-child, .skill-title .name:first-child {
	margin-left: 0;
}

/* Resume */
.resume-items .resume-item {
	padding: 10px 0;
}

.resume-items .resume-item:after {
	left: 0;
}

.resume-items .resume-item:last-child {
	padding-bottom: 0;
}

.resume-items .resume-item:last-child:after {
	display: none;
}

.resume-items .resume-item.active .date {
	border-color: $active-color;
}

.resume-items .resume-item .date {
	margin: 0 0 11px 0;
	padding: 0 5px;
	font-size: 13px;
	line-height: 18px;
	color: #ccc;
	font-weight: 400;
	text-transform: none;
	border: 1px solid #eeeeee;
}

.resume-items .resume-item .skill {	margin: 0 0 11px 0;
	padding: 0 5px;
	font-size: 13px;
	line-height: 18px;
	color: #ccc;
	font-weight: 400;
	text-transform: none;
	border: 1px solid #eeeeee;
	border-color: $active-color;
}

.resume-items .resume-item .date:before {
	display: none;
}

.resume-items .resume-item .name {
	font-size: 17px;
	color: $dark-color;
	font-weight: 500;
	text-transform: none;
}

.resume-items .resume-item .company {
	margin: 0 0 11px 0;
	font-size: 14px;
	color: #999999;
	font-weight: 400;
	text-transform: none;
}

.resume-items .resume-item p {
	margin: 0;
}

.skills-list.list ul li .name {
	font-size: 16px;
	line-height: 18px;
}

/*
	Works
*/
.content.works .border-line-h:after {
	display: none;
}

.content.works .filter-menu {
	font-weight: 400;
	z-index: 2;
}

.content.works .filter-menu .f_btn {
	font-size: 14px;
	color: $default-color;
}

.content.works .filter-menu .f_btn input {
	position: absolute;
	left: 0;
	top: 0;
	opacity: 0;
	cursor: pointer;
}

.content.works .grid-items.border-line-v:after {
	background: -moz-radial-gradient(top, ellipse cover, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
	background: -webkit-radial-gradient(top, ellipse cover, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
	background: radial-gradient(ellipse at top, #dddddd 0%, rgba(255, 255, 255, 0) 70%);
}

.content.works .grid-items .grid-item.col {
	padding-top: 0;
	padding-bottom: 0;
	margin-top: 30px;
}

@media (max-width: 560px) {
	.content.works .filter-menu {
		position: relative;
		top: 5px;
		left: 0;
		right: auto;
	}
	.content.works .filter-menu button {
		margin: 0 15px 0 0;
	}
}

/* Box Items */
.box-item {
	overflow: visible;
}

.content.blog .box-item {
	text-align: initial;
}

.content.blog .row .col {
	padding-bottom: 0;
}

.box-item .image {
	overflow: hidden;
	z-index: 2;
}

.box-item .image a img {
	transition: all 0.6s ease 0s;
	-moz-transition: all 0.6s ease 0s;
	-webkit-transition: all 0.6s ease 0s;
	-o-transition: all 0.6s ease 0s;
	transform: scale(1);
	-webkit-transform: scale(1);
}

.box-item:hover .image a img {
	transform: scale(1.05);
	-webkit-transform: scale(1.05);
}

.box-item .desc {
	padding: 15px 0;
}

.content.works .box-item .desc {
	padding: 15px 0 0 0;
}

.content.blog .box-item .desc {
	padding-bottom: 0;
	text-align: center;
}

.box-item .desc .name {
	padding: 0;
	display: block;
	font-size: 17px;
	color: $dark-color;
	font-weight: 500;
	text-transform: none;
	word-break: break-word;
	transition: all 0.3s ease 0s;
	-moz-transition: all 0.3s ease 0s;
	-webkit-transition: all 0.3s ease 0s;
	-o-transition: all 0.3s ease 0s;
}

.content.blog .box-item .desc .name {
	padding-bottom: 5px;
	font-size: 17px;
	font-weight: 500;
	text-transform: none;
}

.box-item .desc .category {
	margin: 0;
	font-size: 14px;
	color: #999999;
	text-transform: none;
}

.content.blog .box-item .desc {
	padding-bottom: 20px;
}

.content.works .box-item .desc .category {
	font-size: 14px;
}

.box-item:hover .desc .name {
	color: $active-color;
}

@media (max-width: 560px) {
	.content.works .filter-menu .f_btn {
		margin-left: 0;
		margin-right: 15px;
	}
}

/*
	Contacts
*/
.content.contacts .map {
	margin-bottom: 30px;
	height: 240px;
}

.contact_form .row p {
	display: none;
}

.card-wrap .content.contacts .row .row .col {
	padding-bottom: 30px;
}

.card-wrap .content.contacts .row .row .col.col-d-6:nth-child(1) {
	padding-right: 15px;
}

.card-wrap .content.contacts .row .row .col.col-d-6:nth-child(2) {
	padding-left: 15px;
}

.contact_form input[type="checkbox"], .contact_form input[type="radio"] {
	margin: 0 5px 0 0;
	display: inline;
	width: auto;
	height: auto;
}
.checkbox label br {display: none;}

/*
	Popups
*/
.mfp-wrap.popup-box-inline {
	overflow-y: scroll; /* has to be scroll, not auto */
	-webkit-overflow-scrolling: touch;
}

.grid-items {
	min-height: 1px;
}

.col .centre-image
{
    display: flex;
    justify-content: center;
    align-items: center;
}
#skills {
	align-items: center;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
  
	div {
	  padding: 0.2rem;
	  transition: opacity 0.5s;
	}
  
	div[skill-type="language"] {
	  color: #6dabcc;
	}
  
	div[skill-type="framework"] {
	  color: #3b77ca;
	}
  
	div[skill-type="tool"] {
	  color: #042cce;
	}
  
	div[skill-weight="1"] {
	  font-size: 0.75rem;
	  line-height: 0.25rem;
	}
  
	div[skill-weight="2"] {
	  font-size: 1.25rem;
	  line-height: 0.75rem;
	}
  
	div[skill-weight="3"] {
	  font-size: 1.75rem;
	  line-height: 1.25rem;
	}
  
	div[skill-weight="4"] {
	  font-size: 2.25rem;
	  line-height: 1.75rem;
	}
  
	div[skill-weight="5"] {
	  font-size: 2.75rem;
	  line-height: 2.25rem;
	}
  
	input[type="radio"] {
	  display: none;
	}
  
	.filter-button {
	  display: none;
	}
  }